// 工具函数！
import moment from "moment"; // 日期处理包

// 将时间戳格式化
export function toTime(num){
    // new Date(1593676984070) ==> 数字时间戳变成日期对象
    // moment(日期对象).format(参数)
    console.log(moment(new Date(1593676984070)).format('YYYY-MM-DD HH:MM:SS'))
    let time = new Date(num);
    return moment(time).format('YYYY-MM-DD HH:mm:ss');
}

