// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

// 导入element-ui 的包和样式！
import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';
import "../static/style/theme/index.css"  // 引入自定义主题样式
Vue.use(ElementUI)


// 仓库
import store from "@/store"

// 过滤器函数
import {  toTime } from "@/common/utils"
Vue.filter('toTime', toTime)

// 接口地址
import { HOST } from "@/common/apiUrl" 
Vue.prototype.$HOST = HOST

// 安装全屏插件
import fullscreen from 'vue-fullscreen'
Vue.use(fullscreen)




Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})


// 打包注意事项：
  // 修改： config/index.js 
    /*
        // 原来的  54行左右
        assetsSubDirectory: 'static',
        assetsPublicPath: '/',
        // 改为
        assetsSubDirectory: './static',
        assetsPublicPath: './',
    */

  // 修改： build/utils.js 
  /*
      // 原来的  49行左右
        use: loaders,
        fallback: 'vue-style-loader',
      // 改为
        use: loaders,
        fallback: 'vue-style-loader',
        publicPath: '../../'
  */

// 上述两处修改之后，执行 npm run build 命令！ 生成dist目录。将dist目录里面的index.html和static文件夹交给后台部署即可  


