//================== 区分环境，配置接口地址
let host, api_url;
if (process.env.NODE_ENV == "development") {  // 开发环境
    host = "http://localhost:3000",
        // api_url = "http://localhost:3000/api"
        api_url = "/api"
}
if (process.env.NODE_ENV == "production") {  // 生成环境
    host = "http://localhost:3000",
        api_url = "http://localhost:3000/api"
}   

export let HOST = host;
export let API_URL = api_url;



